               // ---------------modal-------------
// visually-hidden

    .abaut__modal{
    width: 100%;
    max-width: 280px;
    min-height: 459px;
    padding: 28px;
    position: absolute;
    background-color: #FCFCFC;
    border: 2px solid #F8D24E;
    border-radius: 10px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms var(--cubic);
    .backdrop.visually-hidden & { 
    transform: translate(-50%, -50%) scale(0);
    }
    @media screen and (min-width: 1200px) {
        max-width: 568px;
        min-height: 449px;
        padding: 32px 60px;
        }
}

.modal__btn{
    position: absolute;
    cursor: pointer;
    padding: 0;
    background-color: transparent;
    right: 8px;
    top: 8px;
    width: 16px;
    height: 16px;
    @media screen and (min-width: 1200px){
        right: 20px;
        top: 20px;
        width: 20px;
        height: 20px;
    }
}

.icon__cloused{
    display: block;
    margin-left: auto;
    margin-right: auto;
    fill:#131313;
    @media screen and (min-width: 1200px){
    }
}
.abaut__modal__title{
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    display: block;
    margin-bottom: 20px;
    text-align: center;
    @media screen and (min-width: 1200px) {
        font-size: 20px;
        line-height: 0.7;
        margin-bottom: 32px;
    }
}

thumb-wrap {
    height: 0;
    overflow: hidden;
    padding-bottom: 0; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
    // 56.25%
}

.thumb-wrap iframe {
    margin: 0 auto;
    width: 224px;
    height: 199px;
    border-radius: 10px;
    border-width: 0;
    outline-width: 0;
    @media screen and (min-width: 1200px) {
        width: 448px;
        height: 205px;
    }
}

.abaut-modal__text{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.42;
    margin-top: 20px;
    display: block;
    @media screen and (min-width: 1200px){
        margin-top: 32px;
        font-size: 14px;
        line-height: 1.37;
        margin-top: 32px;
    }
}